/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { RawHTML } from '@wordpress/element';
import { dateI18n, format, __experimentalGetSettings } from '@wordpress/date';
import ServerSideRender from '@wordpress/server-side-render';
import { useState } from 'react';
import { FormTokenField } from '@wordpress/components';


/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, QueryControls, ToggleControl, SelectControl, RangeControl } from '@wordpress/components';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit({ attributes, setAttributes }) {

	const { numberOfItems, displayDate, displayExcerpt, displayImage, orderEventBy, displayAvailability, eventListingLayout, eventTypes, displayLocation, numberOfTileColumns, displayMonthSeperators, displayIcons, displayTimes, imageSize, imageAlignment, displayBookButton, hashtags, productIDs, displayPrice } = attributes;

	const apiFetch = wp.apiFetch;
	const { addQueryArgs } = wp.url;

	const getProductCategories = (queryArgs) => {
		return apiFetch({
			path: addQueryArgs('wc/store/products/categories', {
				per_page: 0,
				...queryArgs,
			}),
		});
	};

	const productCategories = [];
	getProductCategories().then((categories) => {
		categories.forEach( function( category, index ) {
			productCategories.push( category.slug );
		});	
	});

	const onChangeNumberOfItems = (val) => {
		setAttributes({ numberOfItems: val });
	};

	const onChangeNumberOfTileColumns = (val) => {
		setAttributes({ numberOfTileColumns: val });
	};

	return (
		<>
			<InspectorControls>
				<PanelBody title={__('Event Settings', 'woocommerce-events')}>
					<RangeControl
						label={__('Number of events', 'woocommerce-events')}
						value={numberOfItems}
						onChange={onChangeNumberOfItems}
						min={1}
						max={50}
					/>
					<SelectControl
						label={__('Order', 'woocommerce-events')}
						value={orderEventBy}
						options={[
							{ label: 'DESC', value: 'desc' },
							{ label: 'ASC', value: 'asc' },
						]}
						onChange={(value) =>
							setAttributes({ orderEventBy: value })
						}
					/>
					<SelectControl
						label={__('Event Types', 'woocommerce-events')}
						value={eventTypes}
						options={[
							{ label: 'All events', value: 'all' },
							{ label: 'Single and multi-day', value: 'single-multi' },
							{ label: 'Bookings', value: 'bookings' }
						]}
						onChange={(value) =>
							setAttributes({ eventTypes: value })
						}
					/>
					<FormTokenField
						label={__('Filter by Category slugs', 'woocommerce-events')}
						value={hashtags}
						suggestions={productCategories}
						onChange={ ( tokens ) => {
							const filteredTokens = [];
							tokens.forEach( function( item, index ) {
								const replacement = item.replace( '#', '' );
								const hashtag = replacement;
								if ( typeof hashtag !== 'undefined' ) {
									filteredTokens.push( hashtag );
								}
							} );
							setAttributes( { hashtags: filteredTokens } );
						} }
					/>
					<FormTokenField
						label={__('Filter by Product IDs', 'woocommerce-events')}
						value={productIDs}
						onChange={ ( tokens ) => {
							const filteredTokens = [];
							tokens.forEach( function( item, index ) {
								const replacement = item.replace( '#', '' );
								const productID = replacement;
								if ( typeof productID !== 'undefined' ) {
									filteredTokens.push( productID );
								}
							} );
							setAttributes( { productIDs: filteredTokens } );
						} }
					/>
				</PanelBody>
				<PanelBody title={__('Display Settings', 'woocommerce-events')} initialOpen={false}>
					<SelectControl
						label={__('Layout', 'woocommerce-events')}
						value={eventListingLayout}
						options={[
							{ label: 'List', value: 'list' },
							{ label: 'Tiles', value: 'tiles' },
							{ label: 'Compact', value: 'compact' }
						]}
						onChange={(value) =>
							setAttributes({ eventListingLayout: value })
						}
					/>
					{'tiles' == eventListingLayout && (
						<RangeControl
							label={__('Number of tile columns', 'woocommerce-events')}
							value={numberOfTileColumns}
							onChange={onChangeNumberOfTileColumns}
							min={2}
							max={6}
						/>
					)}
					<ToggleControl
						label={__('Display Image', 'woocommerce-events')}
						checked={displayImage}
						onChange={() =>
							setAttributes({ displayImage: !displayImage })
						}
					/>
					{displayImage && (
						<SelectControl
							label={__('Image size', 'woocommerce-events')}
							value={imageSize}
							options={[
								{ label: 'Thumbnail', value: 'thumbnail' },
								{ label: 'Medium', value: 'medium' },
								{ label: 'Large', value: 'large' },
								{ label: 'Full', value: 'full' }
							]}
							onChange={(value) =>
								setAttributes({ imageSize: value })
							}
						/>
					)}
					{displayImage && (
						<SelectControl
							label={__('Image Alignment', 'woocommerce-events')}
							value={imageAlignment}
							options={[
								{ label: 'Left', value: 'left' },
								{ label: 'Right', value: 'right' }
							]}
							onChange={(value) =>
								setAttributes({ imageAlignment: value })
							}
						/>
					)}

					<ToggleControl
						label={__('Display Month Seperators', 'woocommerce-events')}
						checked={displayMonthSeperators}
						onChange={() =>
							setAttributes({ displayMonthSeperators: !displayMonthSeperators })
						}
					/>
					<ToggleControl
						label={__('Display Location', 'woocommerce-events')}
						checked={displayLocation}
						onChange={() =>
							setAttributes({ displayLocation: !displayLocation })
						}
					/>
					<ToggleControl
						label={__('Display Icons', 'woocommerce-events')}
						checked={displayIcons}
						onChange={() =>
							setAttributes({ displayIcons: !displayIcons })
						}
					/>
					<ToggleControl
						label={__('Display Date', 'woocommerce-events')}
						checked={displayDate}
						onChange={() =>
							setAttributes({ displayDate: !displayDate })
						}
					/>
					<ToggleControl
						label={__('Display Times', 'woocommerce-events')}
						checked={displayTimes}
						onChange={() =>
							setAttributes({ displayTimes: !displayTimes })
						}
					/>
					<ToggleControl
						label={__('Display Excerpt', 'woocommerce-events')}
						checked={displayExcerpt}
						onChange={() =>
							setAttributes({ displayExcerpt: !displayExcerpt })
						}
					/>
					<ToggleControl
						label={__("Display Price", 'woocommerce-events')}
						checked={displayPrice}
						onChange={() =>
							setAttributes({ displayPrice: !displayPrice })
						}
					/>
					<ToggleControl
						label={__('Display Availability', 'woocommerce-events')}
						checked={displayAvailability}
						onChange={() =>
							setAttributes({ displayAvailability: !displayAvailability })
						}
					/>
					<ToggleControl
						label={__("Display Button", 'woocommerce-events')}
						checked={displayBookButton}
						onChange={() =>
							setAttributes({ displayBookButton: !displayBookButton })
						}
					/>
				</PanelBody>
			</InspectorControls>
			<div {...useBlockProps()}>
				<ServerSideRender
					block="woocommerce-events/fooevents-event-listing"
					attributes={attributes}
				/>
			</div>
		</>
	);
}
